// mockjs
const Mock = require('mockjs')
//  mock.Random 
const Random = Mock.Random

// mockjs
let emailList = []
for (let i = 0; i < 100; i++) {
    let newObject = {
        date: Random.datetime(), // 
        theme: Random.cname()// 
    }
    emailList.push(newObject)
}
Mock.mock('/api/emailList', () => {
    return {
        code: '0',
        data: emailList
    }
})
