<template>
    <div id="record">
        <div style="padding-left: 10px; border-left: 5px solid #5B9BD5;">\</div>
        <el-row style="margin:20px 40px 0 40px;">
            <el-col :span="18">
                <div class="searchWord" style="font-size: 14px;">
                    <div style="display: inline-block;margin-bottom: 10px;">
                        
                        <el-input v-model="searchName" style="margin-right:20px;width:120px;display: inline-block;"
                                  placeholder="">
                        </el-input>
                    </div>
                    <div style="display: inline-block;margin-bottom: 10px;">
                        \
                        <el-select v-model="searchInOut" style="width: 120px;">
                            <el-option label="" value=""></el-option>
                            <el-option label="" value=""></el-option>
                            <el-option label="" value=""></el-option>
                        </el-select>
                    </div>
                    <div style="display: inline-block;margin-bottom: 10px;margin-left: 20px;">
                        
                        <el-select v-model="searchCenter" style="width: 150px;">
                            <el-option label="" value=""></el-option>
                            <el-option label="" value=""></el-option>
                            <el-option label="" value=""></el-option>
                            <el-option label="" value=""></el-option>
                            <el-option label="" value=""></el-option>
                        </el-select>
                    </div>
                    <div style="display: inline-block;margin-bottom: 10px;margin-left: 20px;">
                        
                        <el-select v-model="searchDept" style="width: 150px;">
                            <el-option label="" value=""></el-option>
                            <el-option label="" value=""></el-option>
                            <el-option label="" value=""></el-option>
                            <el-option label="" value=""></el-option>
                        </el-select>
                    </div>
                    <div class="block" style="display: inline-block;font-size: 14px;">
                        <span class="date"></span>
                        <el-date-picker style="width: 150px;"
                                        v-model="startTime"
                                        type="datetime"
                                        placeholder="">
                        </el-date-picker>
                    </div>
                    <div class="block" style="display: inline-block;margin-left: 20px;font-size: 14px;">
                        <span class="date"></span>
                        <el-date-picker style="width: 150px;"
                                        v-model="endTime"
                                        type="datetime"
                                        placeholder="">
                        </el-date-picker>
                    </div>
                    <el-button style="margin-left: 20px;" type="primary" @click="search" size="small"></el-button>
                </div>

                <el-table id="table-record" style="margin-top:20px;border-top: 2px solid rgb(91,155,213);"
                          :data="tableData_record"
                          :current-page="currentPage">
                    <el-table-column width="50" prop type="index" :index="indexMethod">
                    </el-table-column>

                    <el-table-column label="" prop="date">
                    </el-table-column>
                    <el-table-column label="" prop="name">
                    </el-table-column>
                    <el-table-column label="\" prop="in_out">
                    </el-table-column>
                    <el-table-column label="" prop="center">
                    </el-table-column>
                    <el-table-column label="" prop="dept">
                    </el-table-column>
                </el-table>
                <el-row class="pagination" style="margin-top: 10px; float: right;">
                    <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-size="PageSize"
                            layout="total, prev, pager, next, jumper"
                            :total="total">
                    </el-pagination>
                </el-row>
            </el-col>
            <el-col class="quantity" :span="4" style="margin-left: 50px;" v-if="this.userInfo.roleId==6">
                <el-button circle type="success" @click="goToLink" style="text-align:center;width: 100px; height: 100px;font-size: 30px;line-height: 17px;"><br></br></el-button>
            </el-col>
            <el-col v-if="this.userInfo.roleId==10">
                <p>xx</p>
                <el-button type="primary" @click="goToRecordApplication"></el-button>
            </el-col>
            <el-col v-if="this.userInfo.roleId==9">
                <p>xxxx</p>
                <el-button type="primary" @click="goToRecordApplication"></el-button>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    var listJson = {
        tableData_record:[{
            date: '2020-07-22',
            name: '',
            in_out: '',
            center: '',
            dept: ''
        },{
            date: '2020-07-22',
            name: '',
            in_out: '',
            center: '',
            dept: ''
        },{
            date: '2020-07-22',
            name: '',
            in_out: '',
            center: '',
            dept: 'xx'
        },],
    }
    export default {
        name: "Record",
        data(){
            return{
                //
                tableData_record:[],
                data:[],
                //1
                currentPage:1,
                //
                PageSize:6,
                //
                total: null,
                //
                searchName: '',
                searchInOut: '',
                searchCenter: '',
                searchDept:'',
                //
                startTime:'',
                endTime:'',
                //MOCK
                // userInfo: JSON.parse(sessionStorage.getItem('userInfo'))
                //storeuser
                userInfo: JSON.parse(this.$store.state.user)
            }

        },
        methods:{
            pageList(){
                // ,
                this.data = listJson.tableData_record;
                this.getList();
            },
            // 
            getList() {
                // es6list
                let tableData_record = this.data.filter((item, index) =>
                    item.name.includes(this.searchName) && item.in_out.includes(this.searchInOut) && item.center.includes(this.searchCenter)
                    && item.dept.includes(this.searchDept)
                );

                //
                this.tableData_record = tableData_record.filter((item, index) =>
                    index < this.currentPage * this.PageSize && index >= this.PageSize * (this.currentPage - 1)
                );
                //
                this.total = tableData_record.length;
            },
            indexMethod(index){
                return (index+1)+(this.currentPage-1)*this.PageSize;
            },
            // getData() {
            // console.log('')
            // this.axios.get('../../static/database.json').then(response => {
            // console.log(response.data);
            // this.tableData_record=response.data
            // }, response => {
            // console.log("error");
            // });
            // },
            // 
            // 
            handleSizeChange(val) {
                // 
                this.PageSize=val;
                this.getList();
            },
            // 
            handleCurrentChange(val) {
                // 
                this.currentPage=val;
                this.getList();
            },
            search(){
                this.currentPage=1;// 
                this.getList();
            },
            //
            goToLink(){
                this.$router.replace('/checkinorout')
            },
            //
            goToRecordApplication(){
                this.$router.replace('/record_application')
            },
        },mounted() {
            // this.getUserInfo();
        },
        created(){
            // this.getData()
            this.pageList();
        },
    }
</script>

