<template>
    <div id="complaint">
        <div style="padding-left: 10px; border-left: 5px solid #5B9BD5;">\</div>
        <el-row style="margin:20px 40px 0 40px;">
            <el-col :span="12" v-if="this.userInfo.roleId<=6">
                <div class="block" style="display: inline;font-size: 14px;">
                    <span class="date"></span>
                    <el-date-picker style="width: 150px;"
                                    v-model="startTime"
                                    type="date"
                                    placeholder="">
                    </el-date-picker>
                </div>
                <div class="block" style="display: inline;margin-left: 20px;font-size: 14px;">
                    <span class="date"></span>
                    <el-date-picker style="width: 150px;"
                                    v-model="endTime"
                                    type="date"
                                    placeholder="">
                    </el-date-picker>
                </div>
                <div class="searchWord" style="font-size: 14px;margin-top: 10px;">
                    <div style="display: inline-block;">
                        
                        <el-input v-model="searchData" style="margin-right:20px;width:150px;display: inline-block;"
                                  placeholder="">
                        </el-input>
                        <el-button type="primary" @click="search" size="small"></el-button>
                    </div>
                </div>
                <el-table id="table-complaint" style="margin-top:10px;border-top: 2px solid rgb(91,155,213);"
                          :data="tableData_complaint"
                          :current-page="currentPage">
                    <el-table-column width="50" prop type="index" :index="indexMethod">
                    </el-table-column>
                    <el-table-column prop="theme" label="">
                        <template slot-scope="scope">
                            <router-link to="/complaint_detail" style="text-decoration: transparent;color: #606266;">{{scope.row.theme}}</router-link>
                        </template>
                    </el-table-column>
                    <el-table-column label="" prop="date" width="120">
                    </el-table-column>
                </el-table>
                <el-row class="pagination" style="margin-top: 10px;">
                    <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-size="PageSize"
                            layout="total, prev, pager, next, jumper"
                            :total="total">
                    </el-pagination>
                </el-row>
            </el-col>
            <el-col :span="6" v-if="this.userInfo.roleId>=7">
                <p>
                </p>
                <el-button type="primary" @click="goToComplaintApplication" style="float: right;"></el-button>
            </el-col>
        </el-row>
