<template>
    <div class="entity_detail">
        <div style="padding-left: 10px; border-left: 5px solid rgb(91,155,213);">
            <router-link to="/home" style="color: #606266;"></router-link>
            \
        </div>
        <!---->
        <el-row id="entity_info">
            <p class="entity_p">:</p>
            <p class="entity_p">:</p>
            <p class="entity_p">:</p>
<!--            <p class="todo_p"></p>&lt;!&ndash;&ndash;&gt;-->
            <!---->
            <p class="entity_p">:</p>
            <p class="entity_p">:</p>
            <p class="entity_p" style="text-align: right;">:</p>
            <p class="entity_p"style="text-align: right;">:</p>
        </el-row>
        <el-row id="entity_info_button">
            <el-button type="primary" class="application_pass" @click="pass"></el-button>
            <el-button type="danger" class="application_reject" @click="reject"></el-button>
        </el-row>
    </div>
</template>

<script>
    export default {
        name: "Entity_detail",
        methods: {
            pass() {
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            reject() {
                this.$confirm('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'

                })
                    .then(() => {
                    this.$message({
                        type: 'success',
                        message: ''
                    });
                })
                    .catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },

        }
    }
</script>

<style>
    #entity_info{
        width: 60%; border-top: 2px solid rgb(91,155,213);margin-top:20px;margin-left: 40px;background-color: white;padding: 0 20px 0 20px;line-height: 30px
    }

    #entity_info .entity_p{
        border-bottom: 1px solid #eeeeee;
    }
    #entity_info_button{
        margin-top:20px;margin-left: 40px;
    }
</style>