<template>
    <div id="email">
        <div style="padding-left: 10px; border-left: 5px solid #5B9BD5;"></div>
        <el-row style="margin:20px 40px 0 40px;">
            <el-col :span="12">
                <el-table id="table-email" style="margin-top:10px;border-top: 2px solid rgb(91,155,213);"
                          :data="tableData_email"
                          :current-page="currentPage">
                    <el-table-column width="40" prop type="index" :index="indexMethod">
                        <template slot-scope="scope">
                            <img src="../../assets/mail.png" width="15" v-if="!isRead">
                            <img src="../../assets/mail_read.png" width="15" v-if="isRead">
                        </template>
                    </el-table-column>
                    <el-table-column prop="theme" label="">
                        <template slot-scope="scope">
                            <div class="email_theme" @click="selectEmail">{{scope.row.theme}}</div>
<!--                            <router-link to="/email_detail" style="text-decoration: transparent;color: #606266;">{{scope.row.theme}}</router-link>-->
                        </template>
                    </el-table-column>
                    <el-table-column label="" prop="date" width="120">
                    </el-table-column>
                </el-table>
                <el-row class="pagination" style="margin-top: 10px;">
                    <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-size="PageSize"
                            layout="total, prev, pager, next, jumper"
                            :total="total">
                    </el-pagination>
                </el-row>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    import router from "../../router";

    var listJson = {
        tableData_email:[{
            theme: '',
            date: '2020-07-22'
        }, {
            theme: '',
            date: '2020-07-22'
        }, {
            theme: '',
            date: '2020-07-22'
        },{
            theme: '',
            date: '2020-07-22'
        }, {
            theme: '',
            date: '2020-07-22'
        }, {
            theme: '',
            date: '2020-07-22'
        },{
            theme: '',
            date: '2020-07-22'
        }, {
            theme: '',
            date: '2020-07-22'
        }, {
            theme: '',
            date: '2020-07-22'
        },{
            theme: '',
            date: '2020-07-22'
        }, {
            theme: '',
            date: '2020-07-22'
        }, {
            theme: '',
            date: '2020-07-22'
        },{
            theme: '',
            date: '2020-07-22'
        }, {
            theme: '',
            date: '2020-07-22'
        }, {
            theme: '',
            date: '2020-07-22'
        },{
            theme: '',
            date: '2020-07-22'
        }, {
            theme: '',
            date: '2020-07-22'
        },],
    }
    export default {
        name: "Email",
        data(){
            return{
                //
                tableData_email:[],
                data:[],
                //1
                currentPage:1,
                //
                PageSize:12,
                //
                total: null,
                //
                isRead: false,
                //MOCK
                userInfo: JSON.parse(sessionStorage.getItem('userInfo'))
            }
        },
        mounted() {
            this.getUserInfo();
        },
        methods:{
            pageList(){
                // ,
                this.data = listJson.tableData_email;
                this.getList();
            },
            // 
            getList() {
                // es6list
                let tableData_email = this.data.filter((item, index) =>
                    item.theme
                );
                //
                this.tableData_email = tableData_email.filter((item, index) =>
                    index < this.currentPage * this.PageSize && index >= this.PageSize * (this.currentPage - 1)
                );
                //
                this.total = tableData_email.length;
            },
            indexMethod(index){
                return (index+1)+(this.currentPage-1)*this.PageSize;
            },
            // getData() {
            // console.log('')
            // this.axios.get('../../static/database.json').then(response => {
            // console.log(response.data);
            // this.tableData_email=response.data
            // }, response => {
            // console.log("error");
            // });
            // },
            // 
            // 
            handleSizeChange(val) {
                // 
                this.PageSize=val;
                this.getList();
            },
            // 
            handleCurrentChange(val) {
                // 
                this.currentPage=val;
                this.getList();
            },
            search(){
                this.currentPage=1;// 
                this.getList();
            },
            //
            selectEmail(){
                router.push({
                    path:'/email_detail',
                    query:{emailId:this.data.index}
                })
            }
        },
        created(){
            // this.getData()
            this.pageList();
        },
    }
</script>

<style>
#email .email_theme{
    cursor: pointer;
}
</style>