<template>
    <div id="register">
        <el-row style="height: 80px;background-color: rgb(255,255,255,.9);">
<!--            <img src="../../assets/logo.png" style="position: absolute;bottom: 10px;left: 100px;">-->
            <span style="position: absolute;bottom: 10px;left: 450px;font-size: 24px;"></span>
        </el-row>
        <div class="login-container">
            <el-form :model="registerForm" :rules="registerRules" label-position="left" ref="registerForm" label-width="100px" class="login-form" style="width: 800px;margin:60px auto;padding: 30px 50px 10px 20px">
                <el-row>
                    <el-col :span="12">
                        <el-form-item prop="name" label="">
                            <el-input v-model="registerForm.name"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item style="padding-left: 20px;" prop="gender" label="">
                            <el-select v-model="registerForm.gender">
                                <el-option label="" value="1"></el-option>
                                <el-option label="" value="2"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item prop="phone" label="">
                            <el-input v-model="registerForm.phone"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item style="padding-left: 20px;" prop="email" label="">
                            <el-input v-model="registerForm.email"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item prop="company" label="">
                            <el-select v-model="registerForm.company">
                                <el-option label="" value="1"></el-option>
                                <el-option label="" value="2"></el-option>
                                <el-option label="" value="3"></el-option>
                                <el-option label="" value="4"></el-option>
                                <el-option label="" value="5"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item style="padding-left: 20px;" prop="dep" label="">
                            <el-select v-model="registerForm.dep">
                                <el-option label="" value="1"></el-option>
                                <el-option label="" value="2"></el-option>
                                <el-option label="" value="3"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item prop="grp" label="">
                            <el-select v-model="registerForm.grp">
                                <el-option label="1" value="grp1"></el-option>
                                <el-option label="2" value="grp2"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item style="padding-left: 20px;" prop="area" label="">
                            <el-input v-model="registerForm.area"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="" prop="pass0">
                            <el-input type="password" v-model="registerForm.pass0" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item prop="role" label="" style="padding-left: 20px;">
                            <el-select v-model="registerForm.role">
                                <el-option label="" value="7"></el-option>
                                <el-option label="" value="8"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="" prop="pass1">
                            <el-input type="password" v-model="registerForm.pass1" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item style="display: inline-block" prop="answer1" label-width="150px" label="1: ">
                            <el-input v-model="registerForm.answer1" style="display: inline-block"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item style="padding-left: 20px;" prop="answer2" label="2: " label-width="190px">
                            <el-input v-model="registerForm.answer2"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="3: " prop="answer3" label-width="190px">
                            <el-input v-model="registerForm.answer3"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-form-item>
                        <el-button style="float: right;" type="primary" @click="submitForm('registerForm')"></el-button>
                    </el-form-item>
                </el-row>
            </el-form>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Register",
        data() {
            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if (value !== this.registerForm.pass0) {
                    callback(new Error('!'));
                } else {
                    callback();
                }
            };
            return {
                registerForm: {
                    name:'',
                    gender:'',
                    phone:'',
                    email:'',
                    company:'',
                    dep:'',
                    grp:'',
                    area:'',
                    role:'',
                    pass0:'',
                    pass1:'',
                    answer1:'',
                    answer2:'',
                    answer3:'',
                },
                registerRules: {
                    name:[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    gender:[
                        { required: false,}
                    ],
                    phone:[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    email: [
                        { required: true, message: '', trigger: 'blur' },
                        { type: 'email', message: '', trigger: ['blur', 'change'] }
                    ],
                    company:[
                        { required: true, message: '', trigger: 'blur',}
                    ],
                    dep:[
                        { required: true, message: '', trigger: 'blur',}
                    ],
                    grp:[
                        { required: true, message: '', trigger: 'blur',}
                    ],
                    area:[
                        { required: false, }
                    ],
                    role:[
                        { required: true, message: '', trigger: 'blur'}
                    ],
                    pass0:[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    pass1:[
                        { required: true, validator: validatePass, trigger: 'blur' }
                    ],
                    answer1:[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    answer2:[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    answer3:[
                        { required: true, message: '', trigger: 'blur' }
                    ],
                }
            };
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    var user= {
                        email: this.registerForm.email,
                        password: this.registerForm.pass0,
                        userName: this.registerForm.name,
                        gender: Number(this.registerForm.gender),
                        roomNum: this.registerForm.area,
                        phoneNum: this.registerForm.phone,
                        groupName: this.registerForm.grp,
                        depName: this.registerForm.dep,
                        companyName: this.registerForm.company,
                        roleId: Number(this.registerForm.role),
                        answer_1: this.registerForm.answer1,
                        answer_2: this.registerForm.answer2,
                        answer_3: this.registerForm.answer3,
                    };
                    console.log(user)
                    if (valid) {
                        this.$axios.post('/reg', {
                                data: JSON.stringify(user)
                            }
                        )
                        .then((response) => {
                            if (response.status === 200) {
                                this.$message({
                                    message: '',
                                    type: 'success'
                                })
                                this.$router.push('/login')
                            }
                        })
                        .catch(function (error) {
                            console.log(error)
                        })
                    } else {
                        console.log('error submit!!')
                        return false
                    }
                });
            },
        }
    }
</script>

<style>
    #register{
        width: 100vw;
        height: 100vh;
        background-image: url("../../assets/bg.jpg");
    }
</style>