<template>
    <div class="email_detail">
        <div style="padding-left: 10px; border-left: 5px solid rgb(91,155,213);">
            <router-link to="/home" style="color: #606266;"></router-link>
            \
        </div>
        <!---->
        <el-row id="email_info">
            <p class="email_p">:</p>
            <p class="email_p">:</p>
            <p class="email_p">:</p>
            <!--            <p class="todo_p"></p>&lt;!&ndash;&ndash;&gt;-->
            <!---->
            <p class="email_p">:</p>
            <p class="email_p">:</p>
            <p class="email_p" style="text-align: right;">:</p>
            <p class="email_p"style="text-align: right;">:</p>
        </el-row>
        <el-row id="email_info_button">
            <el-button type="primary" class="application_pass" @click="pass"></el-button>
            <el-button type="danger" class="application_reject" @click="reject"></el-button>
        </el-row>
    </div>
</template>

<script>
    export default {
        name: "Email_detail",
        data(){
            return{
                id: this.$route.query.emailId,
                loading: false,
                selectedMail: []
            }
        },
        created(){
            this.fetchData();
        },
        methods: {
            //
            fetchData(){
                this.loading = true;

                //idhttp
                var id = this.id;
                this.$http.get('/api/email/' + id)
                    .then(response => {
                        this.selectedMail = response.data.data;
                        this.selectedMail = response.data.data;
                    }, error => {
                        console.log(error);
                    })

                this.loading = false;
            },
            pass() {
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            reject() {
                this.$confirm('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'

                })
                    .then(() => {
                        this.$message({
                            type: 'success',
                            message: ''
                        });
                    })
                    .catch(() => {
                        this.$message({
                            type: 'info',
                            message: ''
                        });
                    });
            },

        }
    }
</script>

<style>
    #email_info{
        width: 60%; border-top: 2px solid rgb(91,155,213);margin-top:20px;margin-left: 40px;background-color: white;padding: 0 20px 0 20px;line-height: 30px
    }

    #email_info .email_p{
        border-bottom: 1px solid #eeeeee;
    }
    #email_info_button{
        margin-top:20px;margin-left: 40px;
    }
</style>