<template>
    <div id="event">
        <div style="padding-left: 10px; border-left: 5px solid #5B9BD5;">\</div>
        <el-row style="margin:20px 40px 0 40px;">
            <el-col :span="12">
                <div class="block" style="display: inline;font-size: 14px;">
                    <span class="date"></span>
                    <el-date-picker style="width: 150px;"
                                    v-model="startTime"
                                    type="date"
                                    placeholder="">
                    </el-date-picker>
                </div>
                <div class="block" style="display: inline;margin-left: 20px;font-size: 14px;">
                    <span class="date"></span>
                    <el-date-picker style="width: 150px;"
                                    v-model="endTime"
                                    type="date"
                                    placeholder="">
                    </el-date-picker>
                </div>
                <div class="searchWord" style="font-size: 14px;margin-top: 10px;">
                    <div style="display: inline-block;">
                        
                        <el-input v-model="searchData" style="margin-right:20px;width:150px;display: inline-block;"
                                  placeholder="">
                        </el-input>
                        <el-button type="primary" @click="search" size="small"></el-button>
                    </div>
                </div>
                <el-table id="table-event" style="margin-top:10px;border-top: 2px solid rgb(91,155,213);"
                          :data="tableData_event"
                          :current-page="currentPage">
                    <el-table-column width="50" prop type="index" :index="indexMethod">
                    </el-table-column>
                    <el-table-column prop="theme" label="">
                        <template slot-scope="scope">
                            <router-link to="/event_detail" style="text-decoration: transparent;color: #606266;">{{scope.row.theme}}</router-link>
                        </template>
                    </el-table-column>
                    <el-table-column label="" prop="date" width="120">
                    </el-table-column>
                </el-table>
                <el-row class="pagination" style="margin-top: 10px;">
                    <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-size="PageSize"
                            layout="total, prev, pager, next, jumper"
                            :total="total">
                    </el-pagination>
                    <el-button type="primary" @click="goToEventApplication" style="float: right;" v-if="this.userInfo.roleId>6"></el-button>
                </el-row>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    var listJson = {
        tableData_event:[{
            theme: '',
            date: '2020-07-22'
        }, {
            theme: '',
            date: '2020-07-22'
        }, {
            theme: '',
            date: '2020-07-22'
        },{
            theme: '',
            date: '2020-07-22'
        }, {
            theme: '',
            date: '2020-07-22'
        }, {
            theme: '',
            date: '2020-07-22'
        },{
            theme: '',
            date: '2020-07-22'
        }, {
            theme: '',
            date: '2020-07-22'
        }, {
            theme: '',
            date: '2020-07-22'
        },{
            theme: '',
            date: '2020-07-22'
        }, {
            theme: '',
            date: '2020-07-22'
        }, {
            theme: '',
            date: '2020-07-22'
        },{
            theme: '',
            date: '2020-07-22'
        }, {
            theme: '',
            date: '2020-07-22'
        }, {
            theme: '',
            date: '2020-07-22'
        },{
            theme: '',
            date: '2020-07-22'
        }, {
            theme: '',
            date: '2020-07-22'
        },],
    }
    export default {
        name: "Event",
        data(){
            return{
                //
                tableData_event:[],
                data:[],
                //1
                currentPage:1,
                //
                PageSize:6,
                //
                total: null,
                //
                searchData: '',
                //
                startTime:'',
                endTime:'',
                //MOCK
                // userInfo: JSON.parse(sessionStorage.getItem('userInfo'))
                //storeuser
                userInfo: JSON.parse(this.$store.state.user)
            }

        },
        mounted() {
            this.getUserInfo();
        },
        methods:{
            pageList(){
                // ,
                this.data = listJson.tableData_event;
                // this.$axios.post('/event',{
                //
                // })
                this.getList();
            },
            // 
            getList() {
                // es6list
                let tableData_event = this.data.filter((item, index) =>
                    item.theme.includes(this.searchData)
                );
                //
                this.tableData_event = tableData_event.filter((item, index) =>
                    index < this.currentPage * this.PageSize && index >= this.PageSize * (this.currentPage - 1)
                );
                //
                this.total = tableData_event.length;
            },
            indexMethod(index){
                return (index+1)+(this.currentPage-1)*this.PageSize;
            },
            // getData() {
            // console.log('')
            // this.axios.get('../../static/database.json').then(response => {
            // console.log(response.data);
            // this.tableData_event=response.data
            // }, response => {
            // console.log("error");
            // });
            // },
            // 
            // 
            handleSizeChange(val) {
                // 
                this.PageSize=val;
                this.getList();
            },
            // 
            handleCurrentChange(val) {
                // 
                this.currentPage=val;
                this.getList();
            },
            goToEventApplication(){
                this.$router.push("/event_application")
            },
            search(){
                this.currentPage=1;// 
                this.getList();
            },
        },
        created(){
            // this.getData()
            this.pageList();
        },
    }
</script>

